.button {
  background-color: transparent;
  border: none;
  margin: 0;
  padding: 0;
  text-align: inherit;
  font: inherit;
  border-radius: 0;
  appearance: none;
  cursor: pointer;
}

.contentContainer {
  display: flex;
  flex-direction: column;
}

.contentLabel {
  font-weight: bold;
  margin-right: 8px;
}